// 某个模块的数据请求（增删改查！）

import $axios from "@/common/http";  // 封装的axios
import axios from "axios"   // 原生的axios
import { Message } from "element-ui"  // 提示框


export async function getBanner(id) {
    let res;
    if (id) {
        res = await $axios.get("/bannerinfo", { params: { id } });
    } else {
        res = await $axios.get("/bannerlist");  
    }
    return res.list;
}

export async function addBanner(data) {
    let res = await $axios.post("/banneradd", data);
    Message.success(res.msg)
    return true;
}

export async function editBanner(data) {
    let res = await $axios.post("/banneredit", data);
    Message.success(res.msg)
    return true;
}

export async function delBanner(id) {
    if (!id) {
        Message.warning("请传入删除id");
        return;
    }
    let res = await $axios.post("/bannerdelete", { id })
    Message.success(res.msg)
    return res.list;
}


