<template>
  <div>
    <router-link to="/banner/add">
      <el-button type="primary">
      <i class="el-icon-circle-plus-outline"></i> 添加轮播
    </el-button>
    </router-link>
    <div class="table-box">
      <el-table :data="list"  style="width: 100%">
        <el-table-column align="center" prop="title" label="轮播图名称" ></el-table-column>
        <el-table-column align="center" prop="img" label="图片" >
            <template slot-scope="scope">
                  <img :src="$HOST+scope.row.img" class="picimg" alt="">
            </template>
        </el-table-column>
        <el-table-column align="center"  prop="status" label="状态" >
          <template slot-scope="scope">
              <el-tag type="success" v-if="scope.row.status">已启用</el-tag>
              <el-tag type="info" v-else>已禁用</el-tag>  
          </template>
        </el-table-column>
        <el-table-column label="操作"  align="center">
              <template slot-scope="scope">
                    <router-link :to="'/banner/'+scope.row.id"><el-button type="success" icon="el-icon-edit" size="mini" circle  ></el-button></router-link>
                    <el-button type="danger" icon="el-icon-delete" size="mini" @click="delRow(scope.row.id)" circle></el-button>
              </template>
          </el-table-column>
      </el-table>
    </div>
  </div>
</template>

<script>
// 获取数据、删除数据
import { getBanner,delBanner } from "@/request/banner"
export default {
    data(){
        return{
            list:[],
        }
    },
    created(){
        this.getlist();  // 获取列表
    },
    methods:{
        async getlist(){
            this.list = await getBanner();  
        },
        delRow(id){  // 删除某一条数据，接收删除的ID
            // 弹框确认！
            this.$confirm('确定删除吗, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(async () => {
                // 删除功能！
                await delBanner(id);
                this.getlist();
            })
        }
    },
    components:{
    }
}
</script>
<style scoped>
.picimg{
    width:60px;
    border:1px solid #eee;
}
</style>


